/** 
 * Eric Meyer's Reset CSS v2.0 (http://meyerweb.com/eric/tools/css/reset/) * http://cssreset.com 
 */
html, body, div, span, applet, object, iframe,h1, h2, h3, h4, h5, h6, p, blockquote, pre,a, abbr, acronym, address, big, cite, code,del, dfn, em, img, ins, kbd, q, s, samp,small, strike, strong, sub, sup, tt, var,b, u, i, center,dl, dt, dd, ol, ul, li,fieldset, form, label, legend,table, caption, tbody, tfoot, thead, tr, th, td,article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary,time, mark, audio, video {	
	margin: 0;	
	padding: 0;	
	border: 0;	
	font-family: inherit;
    font-style: inherit;
    font-variant: inherit;
    font-weight: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, textarea, img {	
	display: block;
	position: relative;
}

body {	
	line-height: 1;
}

ol, ul {	
	list-style: none;
}

blockquote, q {	
	quotes: none;
}

blockquote:before, blockquote:after,q:before, q:after {	
	content: '';	
	content: none;
}

table {	
	border-collapse: collapse;	
	border-spacing: 0;
}

/**
 * <html> and <body> width/height to 100% 
 */
html, body {
	width: 100%;
	height: 100%;
}

/**
 * Simple <a> base style
 */
a {
	text-decoration: none;
    cursor: pointer;
	color: inherit; //inherit from parent container so you don't have to specically have to declare colors for links.
}

/**
 * Make sure these elements have a set position so they and their children can be positioned (relative to the parent).
 */
div, ul, ol {
	position: relative;
}

/**
 * Apply a natural box layout model to all elements, but allowing components to change
 * See http://www.paulirish.com/2012/box-sizing-border-box-ftw/ (August 2014 update)
 * See http://caniuse.com/#search=box-sizing
 */
html {
	box-sizing: border-box;
}
*, *:before, *:after {
	box-sizing: inherit;
}

